// font
.font_M-Th(){
  font-family: 'Montserrat-Thin';
}
.font_M-M(){
  font-family: 'Montserrat-Medium';
}
.font_M-B(){
  font-family: 'Montserrat-Bold';
}
.font_M-ExB(){
  font-family: 'Montserrat-ExtraBold';
}

// color
@background: #3f3f3f;
@col_orange: #ff6904;
@back-button: #ff6904;
@col-line-top: #252525;
@col-line-bottom: #5a5a5a;
@col_fff: #fff;
@col_black: #000;

// title
.h2(){
  .font_M-ExB();
  .f48();
  color: @col_fff;
}

// size
.f12(){
  font-size: 12px;
}
.f14(){
  font-size: 14px;
}
.f16(){
  font-size: 16px;
}
.f24(){
  font-size: 24px;
}
.f48(){
  font-size: 48px;
}

// pseodo
.screen-line(){
  position: absolute;
  content: '';
  height: 2px;
  width: 86%;
  top: 0;
  left: 7%;
  border-top: 1px solid @col-line-top;
  border-bottom: 1px solid @col-line-bottom;
}