.advantages,
.service,
.faq,
.footer {
  max-width: 1170px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
/* container */
.advantages,
.service,
.faq,
.footer {
  position: relative;
}
.advantages::after,
.service::after,
.faq::after,
.footer::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}
.icon {
  fill: #fff;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
.service-icon {
  font-size: 60px;
}
.s-dot {
  font-size: 5px;
}
.emblem-icon {
  font-size: 50px;
}
.menu-line {
  height: 17px;
  width: 30px;
  flex-shrink: 0;
}
.faq_a-icon {
  height: 14px;
  width: 16px;
  flex-shrink: 0;
}
.faq_q-icon {
  height: 15px;
  width: 16px;
  flex-shrink: 0;
}
.x-icon {
  height: 17px;
  width: 18px;
  flex-shrink: 0;
}
.menu-line:hover,
.x-icon:hover {
  cursor: pointer;
}
.advantages {
  position: relative;
  background-color: #3f3f3f;
  background-image: url("../images/advantages_back.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
  padding-top: 55px;
  padding-bottom: 120px;
}
@media screen and (max-width: 600px) {
  .advantages {
    display: none;
  }
}
.advantages-title {
  font-family: 'Montserrat-ExtraBold';
  font-size: 48px;
  color: #fff;
  margin-bottom: 110px;
}
.advantages-items {
  position: relative;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: auto;
}
.advantages-item:first-child {
  grid-row-start: 1;
}
.advantages-item:nth-child(2) {
  grid-row-start: 2;
}
.advantages-item:nth-child(3) {
  grid-row-start: 3;
}
.advantages-item:nth-child(4) {
  grid-row-start: 4;
}
.advantages-item:nth-child(5) {
  grid-row-start: 5;
}
.advantages-item:nth-child(6) {
  grid-row-start: 6;
}
.advantages-item:nth-child(7) {
  grid-row-start: 7;
  align-items: flex-start;
}
.advantages-item:nth-child(8) {
  grid-row-start: 8;
  align-items: flex-start;
}
.advantages-item {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 30px;
  align-items: center;
  padding: 20px 0;
  font-family: 'Montserrat-Bold';
  font-size: 12px;
  color: #fff;
  text-align: center;
}
.advantages-item__title {
  grid-column-start: 1;
  grid-column-end: 3;
  font-size: 16px;
  text-align: start;
  color: #ff6904;
  margin-right: -35px;
}
@media screen and (max-width: 970px) {
  .advantages-item__title {
    grid-column-end: 4;
    margin-right: 15px;
    word-wrap: break-word;
    overflow-wrap: break-word;
    width: 100%;
  }
}
.advantages-item__goodkassa {
  grid-column-start: 4;
  grid-column-end: 6;
  color: #000;
  margin-left: -15px;
  margin-right: -15px;
  z-index: 55;
}
@media screen and (max-width: 970px) {
  .advantages-item__goodkassa {
    margin-right: -30px;
  }
}
@media screen and (max-width: 768px) {
  .advantages-item__goodkassa {
    grid-column-end: 7;
    margin-right: 15px;
  }
}
.advantages-item .goodkassa-head {
  color: #ff6904;
}
.advantages-item__mts {
  grid-column-start: 6;
  grid-column-end: 8;
  margin-left: 15px;
  margin-right: -30px;
}
@media screen and (max-width: 970px) {
  .advantages-item__mts {
    grid-column-start: 7;
    grid-column-end: 9;
    margin-left: 0;
    margin-right: 0;
  }
}
.advantages-item__modul {
  grid-column-start: 8;
  grid-column-end: 11;
  margin-left: 30px;
  margin-right: 30px;
}
@media screen and (max-width: 970px) {
  .advantages-item__modul {
    grid-column-start: 9;
    grid-column-end: 11;
    margin-left: 0;
    margin-right: 0;
  }
}
.advantages-item__evotor {
  grid-column-start: 11;
  grid-column-end: 13;
  margin-left: -30px;
}
@media screen and (max-width: 970px) {
  .advantages-item__evotor {
    margin-left: 0;
  }
}
@media screen and (max-width: 970px) {
  .advantages-item:nth-child(7) .advantages-item__mts {
    display: none;
  }
