@media screen and (max-width: 970px) {
  .advantages-item:nth-child(7) .advantages-item__modul {
    grid-column-start: 8;
    grid-column-end: 12;
    margin-left: -15px;
    margin-right: -15px;
  }
}
@media screen and (max-width: 970px) {
  .advantages-item:nth-child(7) .advantages-item__evotor {
    display: none;
  }
}
@media screen and (max-width: 970px) {
  .advantages-item:last-child .advantages-item__mts {
    display: none;
  }
}
@media screen and (max-width: 970px) {
  .advantages-item:last-child .advantages-item__modul {
    grid-column-start: 9;
    grid-column-end: 11;
    margin-left: -30px;
    margin-right: -30px;
  }
