.faq{
  position: relative;
  background-color: @background;
  background-image: url("../images/faq_back-light.png");
  background-repeat: no-repeat;
  background-position: 50% 50%;
  padding-top: 70px;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 30px;
  grid-template-rows: 78px 1fr 135px;
  grid-row-gap: 1px;

  .xs-block({
    grid-template-columns: repeat(6, 1fr);
  });

  &-title{
    .h2();
    text-align: center;
    padding-bottom: 85px;
    grid-column-start: 6;
    grid-column-end: 8;

    .xs-block({
      grid-column-start: 3;
      grid-column-end: 5;
    })
  }
  &-items{
    grid-row-start: 2;
    grid-column-start: 2;
    grid-column-end: 12;

    .xs-block({
      grid-column-end: 6;
    })
  }
}
.faq-item {
  &__answer {
    margin-right: -30px;
    padding-top: 20px;
  }
}
  // question
.question{
  &-faq_q{
    margin-right: 15px;
  }
  &-icon__menu{
    margin-left: auto;
    margin-right: 30px;

    .md-block({
      margin-right: 0;
    })
  }
  &-title{
    text-transform: uppercase;
    .font_M-B();
    word-break: break-word;
    color: @col_orange;
    display: flex;
    margin-left: -30px;
    margin-right: -30px;
    padding-top: 20px;
    padding-bottom: 12px;
    border-bottom: 1px solid #fff;
  }
}
  // answer
.answer{
  &-icon__menu{
    margin-left: auto;
    margin-right: 30px;

    .md-block({
      margin-right: 0;
    })
  }
  &-title {
    position: relative;
    text-transform: uppercase;
    .font_M-B();
    color: @col_fff;
  }
  &-head{
    display: flex;
    justify-content: space-between;
  }
  &-content{
    display: flex;
    margin-right: 30px;
    padding-top: 26px;

    &__text{
      color: @col_fff;
    }
  }
  &-faq_a{
    margin-right: 15px;
  }
}
  // * * * *
//  break line
.faq::before{
  .screen-line();
}
