.footer{
  position: relative;
  background: /*background: linear-gradient(to bottom, rgba(63,63,63,1) 0%, rgba(63,63,63,1) 0%, rgba(63,63,63,1) 50%, rgb(33,33,33) 80%);*/ @background;
  padding-top: 90px;
  padding-bottom: 70px;
  color: #fff;


  &-title{
    .h2();
    display: flex;
    justify-content: space-around;
    text-align: center;
    margin-bottom: 114px;
    .md-block({
      .size-md(8);
      justify-content: center;
    });
    .sm-block({
      margin-bottom: 80px;
    });
    .smm-block({
      margin-bottom: 50px;
    });
    .xs-block({
      margin-bottom: 30px;
    });
    .xxs-block({
      margin-bottom: 0;
    })

  }
  &-info{
    margin-bottom: 79px;
    .sm-block({
      margin-bottom: 59px;
    });
    .smm-block({
      margin-bottom: 40px;
    });
    .xs-block({
      margin-bottom: 30px;
    });

    &__list{
      display: flex;
      justify-content: space-around;
      .font_M-M();
      .md-block({
        justify-content: space-between;
      });
      .sm-block({
        flex-wrap: wrap;
        justify-content: space-around;
      });
      .xs-block({
        flex-wrap: nowrap;
        justify-content: center;
        flex-direction: column;
        align-items: center;
      })
    }
  }
  &-copyright{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    .sm-block({
      .row-flex();
      justify-content: center;
    });
    .xs-block({
      flex-direction: column;
      align-items: center;
      text-align: center;
    })
  }
}
.copyright,
.copyright-text{
  .sm-block({
    order: 1;
    padding-top: 20px;
  });
  .size-sm(6);
  .size-xs(8);
  .size-xxs(10);
}
.copyright-text{
  position: relative;
  .sm-block({
    text-align: end;
  });
}
.footer-copyright{
  .copyright-text{
    .size-xs(8);
    .xs-block({
      text-align: center;
    })
  }

}
.copyright-text::after{
  position: absolute;
  content: '';
  bottom: -1px;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: #fff;
  .sm-block({
    width: 191px;
  });
  .xs-block({
    right: calc(50% - 95px);
  })
}

  //  break line
.footer::before{
  .screen-line();

  .xs-block({
    width: 80%;
    left: 10%;
  });
  .xxs-block({
    width: 75%;
    left: 12%;
  })
}
// - - -
.footer-info__list-link{
  .font_M-ExB();
  .f24();
}
.footer-info__list-text,
.footer-copyright{
  .f14();
}
//.footer-info__list-text{}
.line{
  border-bottom: 1px solid;
}
.footer-info__list-item{
  display: flex;
  flex-direction: column;
  .size-md(4.5);
  .size-sm(6.4);
  .size-xs(10);
  .size-xxs(12);

  .md-block({
    align-items: center;
  });
  .sm-block({
    padding: 15px 0;
  });
}

// button
.copyright-button{
  .size(4);
  .size-md(5);
  .size-sm(7);
  .size-xs(9);
  .size-xxs(11);
  display: flex;
  justify-content: center;
  padding: 4px;
  border-radius: 30px;
  border: 1px solid @back-button;

  .sm-block({
    margin-right: 10%;
    margin-left: 10%;
  });
  .xs-block({
    margin-right: 0;
    margin-left: 0;
  });
}
.copyright-btn{
  width: 100%;
  text-align: center;
  border-radius: 26px;
  background-color: @back-button;
  box-shadow: 0 20px 30px 0 rgba(255,105,4,.5);
}
.copyright-btn__link{
  display: block;
  text-transform: uppercase;
  .font_M-ExB();
  .f14();
  padding-top: 20px;
  padding-bottom: 16px;
}
// button effects
.copyright-btn__link:hover{
  cursor: pointer;
}
.copyright-button:active{
  background-color: @back-button;
}






