.advantages,
.service,
.faq,
.footer{
  .wrapper();
}
/* container */
.advantages,
.service,
.faq,
.footer{
  position: relative;
}

.advantages::after,
.service::after,
.faq::after,
.footer::after{
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  z-index: -1;
  margin: auto;
  left: -50vw;
  right: -50vw;
}