.advantages{
  position: relative;
  background-color: @background;
  background-image: url("../images/advantages_back.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
  padding-top: 55px;
  padding-bottom: 120px;

  .xs-block({
    display: none;
  });

  &-title {
    .h2();
    margin-bottom: 110px;
  }
  // I - T - E - M - s
  &-items{
    position: relative;
    text-transform: uppercase;
    display: grid;
    grid-template-columns: 1fr;
    grid-auto-rows: auto;
  }
  // I-T-E-M
  &-item:first-child{
    grid-row-start: 1;
  }
  &-item:nth-child(2){
    grid-row-start: 2;
  }
  &-item:nth-child(3){
    grid-row-start: 3;
  }
  &-item:nth-child(4){
    grid-row-start: 4;
  }
  &-item:nth-child(5){
    grid-row-start: 5;
  }
  &-item:nth-child(6){
    grid-row-start: 6;
  }
  &-item:nth-child(7){
    grid-row-start: 7;
    align-items: flex-start;
  }
  &-item:nth-child(8){
    grid-row-start: 8;
    align-items: flex-start;
  }
}
// -- I-T-E-M --
.advantages-item{
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-column-gap: 30px;
  align-items: center;
  padding: 20px 0;
  .font_M-B();
  .f12();
  color: @col_fff;
  text-align: center;

  &__title{
    grid-column-start: 1;
    grid-column-end: 3;
    .f16();
    text-align: start;
    color: @col_orange;
    margin-right: -35px;

    .md-block({
      grid-column-end: 4;
      margin-right: 15px;
      word-wrap: break-word;
      overflow-wrap: break-word;
      width: 100%;
    })
  }
  &__goodkassa{
    grid-column-start: 4;
    grid-column-end: 6;
    color: @col_black;
    margin-left: -15px;
    margin-right: -15px;
    z-index: 55;

    .md-block({
      margin-right: -30px;
    });

    .sm-block({
      grid-column-end: 7;
      margin-right: 15px;
    })
  }
  .goodkassa-head{
    color: @col_orange;
  }
  &__mts{
    grid-column-start: 6;
    grid-column-end: 8;
    margin-left: 15px;
    margin-right: -30px;

    .md-block({
      grid-column-start: 7;
      grid-column-end: 9;
      margin-left: 0;
      margin-right: 0;
    })
  }
  &__modul{
    grid-column-start: 8;
    grid-column-end: 11;
    margin-left: 30px;
    margin-right: 30px;

    .md-block({
      grid-column-start: 9;
      grid-column-end: 11;
      margin-left: 0;
      margin-right: 0;
    })
  }
  &__evotor{
    grid-column-start: 11;
    grid-column-end: 13;
    margin-left: -30px;

    .md-block({
      margin-left: 0;
    })
  }
}

// cell
.advantages-item:nth-child(7){
  .advantages-item__mts{
    .md-block({
      display: none;
    })
  }
  .advantages-item__modul{
    .md-block({
      grid-column-start: 8;
      grid-column-end: 12;
      margin-left: -15px;
      margin-right: -15px;
    })
  }
  .advantages-item__evotor{
    .md-block({
      display: none;
    })
  }
}
.advantages-item:last-child{
  .advantages-item__mts{
    .md-block({
      display: none;
    })
  }
  .advantages-item__modul{
    .md-block({
      grid-column-start: 9;
      grid-column-end: 11;
      margin-left: -30px;
      margin-right: -30px;
    })
  }
  .advantages-item__evotor{
    .md-block({
      display: none;
    })
  }
}

// border-line
.advantages-item:not(:first-child){
  border-top: 1px solid @col_orange;
}

// back-window with border
.advantages-items::before{
  position: absolute;
  content: '';
  width: 18.1%;
  height: 112%;
  background-color: @col_fff;
  border: 6px solid @col_orange;
  top: -40px;
  left: 24%;
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.6);
  z-index: 1;

  .md-block({
    width: 22.5%;
    left: 22.6%;
  });

  .sm-block({
    width: 26%;
    left: 22%;
  });
}

//  break line
.advantages::before{
  .screen-line();
}