.icon{
  fill: #fff;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  font-size: 25px;
}
// service
.service-icon{
  font-size: 60px;
}
.s-dot{
  font-size: 5px;
}
.emblem-icon{
  font-size: 50px;
}
// f.a.q.
.menu-line{
  height: 17px;
  width: 30px;
  flex-shrink: 0;
}
.faq_a-icon{
  height: 14px;
  width: 16px;
  flex-shrink: 0;
}
.faq_q-icon{
  height: 15px;
  width: 16px;
  flex-shrink: 0;
}
.x-icon{
  height: 17px;
  width: 18px;
  flex-shrink: 0;
}
// hover
.menu-line:hover,
.x-icon:hover{
  cursor: pointer;
}