.service{
  position: relative;
  background-color: @background;
  background-image: url("../images/service_back.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 60%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  .size(12);
  padding-top: 60px;
  padding-bottom: 30px;

  .md-block({
    padding-bottom: 60px;
  });

  .xs-block({
    background-position: 50% 75%;
    padding-bottom: 60px;
  });

  .xxs-block({
    background-position: 50% 80%;
  });

  .xss-block({
    background-position: 50% 82%;
  });

  &-title{
    .h2();
    text-align: center;
    grid-column-start: 2;
    grid-column-end: 12;
    margin-bottom: 65px;

    .md-block({
      grid-column-start: 1;
      grid-column-end: 13;
      grid-row-start: 1;
    });

    .xs-block({
      word-wrap: break-word;
      overflow-wrap: break-word;
    });

    .xxs-block({
      .size(12);
    })
  }
  //  I-T-E-M-s
  &-items{
    grid-column: span 12;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 30px;
    grid-auto-rows: auto;

    .md-block({
      grid-row-start: 2;
      grid-row-end: 5;
      grid-auto-rows: auto;
    });

    .xs-block({
      display: block;
      grid-template-columns: repeat(6, 1fr);
      grid-column: span 6;
    });
  }
  //    I-T-E-M
  &-item{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -50%;

    .xs-block({
      margin-right: 0;
      margin-left: 0;
    });

    &__title,
    &__text{
      text-align: center;
    }
    .s-dot{
      margin: 16px 0;
    }
    &__title{
      text-transform: uppercase;
      .font_M-B();
      .f16();
      color: @col_orange;
      margin-bottom: 25px;
    }
    &__text{
      color: @col_fff;
    }
  }
  &-item:first-child{
    grid-row-start: 1;
    grid-column-start: 6;
    grid-column-end: 8;
    margin-top: 0;
    margin-left: -30px;
    margin-right: -30px;

    .md-block({
      grid-row-start: 1;
      grid-column-start: 6;
      grid-column-end: 8;
    });

    .xs-block({
      grid-row-start: 1;
      grid-column: span 6;
      margin-left: 0;
      margin-right: 0;
    })
  }
  &-item:nth-child(2){
    grid-row-start: 2;
    grid-column-start: 3;
    grid-column-end: 6;
    margin-left: 30px;
    margin-right: 15px;

    .md-block({
      grid-row-start: 4;
      margin-left: 0;
      margin-right: -30px;
    });

    .xs-block({
      grid-row-start: 2;
      grid-column: span 6;
      margin-right: 0;
      margin-top: 22px;
    })
  }
  .service-item:nth-child(2){
    .xs-block({
      margin-right: 0;
    })
  }
  &-item:nth-child(3){
    grid-row-start: 2;
    grid-column-start: 8;
    grid-column-end: 11;
    margin-left: 15px;
    margin-right: 30px;

    .md-block({
      grid-row-start: 4;
      margin-left: 0;
      margin-right: 0;
    });

    .xs-block({
      grid-row-start: 3;
      grid-column: span 6;
      margin-top: 22px;
    })
   }
  &-item:nth-child(4){
    grid-row-start: 3;
    grid-column-start: 1;
    grid-column-end: 4;
    margin-left: 15px;
    margin-right: 30px;

    .md-block({
      grid-row-start: 1;
      margin-top: 0;
      margin-left: 0;
      margin-right: -15px;
    });

    .xs-block({
      grid-row-start: 4;
      grid-column: span 6;
      margin-right: 0;
      margin-top: 22px;
    })
  }
  .service-item:nth-child(4){
    .xs-block({
      margin-right: 0;
    })
  }
  &-item:last-child{
    grid-row-start: 3;
    grid-column-start: 10;
    grid-column-end: 13;
    margin-left: 30px;
    margin-right: 15px;

    .md-block({
      grid-row-start: 1;
      margin-top: 0;
      margin-left: 0;
      margin-right: 0;
    });

    .xs-block({
      grid-row-start: 5;
      grid-column: span 6;
      margin-top: 22px;
    })
  }
  //  - - - - -
  &-image{
    padding-top: 110px;

    .md-block({
      grid-row-start: 7;
    });

    .xs-block({
      display: grid;
      grid-column: span 6;
      justify-content: center;
      margin-left: 0;
      margin-right: 0;
    });
  }
  &-footer{
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 30px;
    grid-column: span 12;
    .font_M-M();
    color: @col_fff;
    align-items: center;
    margin-top: 20px;

    .xs-block({
      grid-template-columns: repeat(6,1fr);
      grid-column: span 6;
    });

    &__item:first-child{
      margin-right: -30px;
      grid-column: span 6;

    }
    &__item:last-child{
      grid-column: span 6;

      .xs-block({
        padding-top: 25px;
      })
    }
    &__item:first-child,
    &__item:last-child{
      display: flex;
      align-items: center;
    }
    &__item-title{
      .font_M-Th();
      font-size: 48px;
      padding-right: 15px;
    }
    &__item-title:last-child{
      display: flex;
      flex-basis: 60%;
      margin-right: 0;
    }
  }
}
//  break line
.service::before{
  .screen-line();
}

